.site-list__item {
  @include list-reset;
  padding: 25px 20px 19px;

  &:nth-child(1) {
    border-top: 1px solid $border-color;
  }

  &:nth-child(2) {
    border-top: 1px solid $border-color;
  }

  &:nth-child(3) {
    border-top: 1px solid $border-color;
    border-bottom: 1px solid $border-color;
  }
}

.site-list__link {
  @include list-reset;
  text-decoration: none;
  color: $black;
  font-family: "Oswald";
  font-weight: 500;
  font-size: 20px;
  text-transform: uppercase;
}

.site-list__item:hover {
  @media (min-width: $tablet-width) {
    opacity: 0.6;
  }
}

.site-list__item:active {
  @media (min-width: $tablet-width) {
    opacity: 0.3;
  }
}

@media (min-width: $mobile-width-only) {}

@media (min-width: $tablet-width) {

  .site-list__link--current-link {
    padding-bottom: 10px;
    border-bottom: 2px solid $basic-green;
  }

  .site-list__item {
    padding: 0;
    padding-top: 18px;
  }

  .site-list__link {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
}

@media (min-width: $desktop-width) {

}
