@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/lato/Lato-Regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/oswald/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../../fonts/oswald/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
}

body {
  background-color: #FFF;
  font-style: normal;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  font-family: "Lato";
  color: #444;
}

* {
  outline: 1px solid #000000;
}

html,
body {
  margin: 0;
  padding: 0;
  font-style: normal;
  box-sizing: border-box;
}

.main-header {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: relative;
  height: 65px;
}

.main-header__container {
  margin: 0;
  padding: 0;
  list-style-type: none;
  max-width: 280px;
  margin: 0 auto;
  justify-content: center;
  padding: 10px 20px;
}

.main-header__header {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
}

.main-header__logo-container {
  margin: 0;
  padding: 0;
  list-style-type: none;
  width: 191px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  height: 37px;
}

.main-header__logo-img {
  margin: 0;
  padding: 0;
  list-style-type: none;
  width: 33px;
  height: 38px;
  vertical-align: middle;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

@media (min-width: 708px) {
  .main-header {
    height: 52px;
    margin-bottom: 62px;
  }

  .main-header__container {
    padding: 0;
  }

  .main-header__logo-container {
    min-width: 174px;
    position: relative;
    height: 52px;
  }

  .logo-text__image {
    position: absolute;
    top: 19px;
    right: 0;
  }

  .main-header__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }

  .main-header__container {
    min-width: 708px;
    margin: 0 auto;
    justify-content: center;
  }
}
@media (min-width: 1220px) {
  .main-header__container {
    min-width: 1220px;
    margin: 0 auto;
    justify-content: center;
  }

  .main-header__logo-container {
    max-width: 202px;
  }

  .main-header__header {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }

  .main-header {
    min-height: 59px;
    position: relative;
  }

  .main-header--bg::after {
    content: "";
    position: absolute;
    display: block;
    width: 50%;
    min-height: 694px;
    top: 0;
    right: 0;
    left: inherit;
    background: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/cat-photo.jpg") no-repeat;
    background-size: cover;
    z-index: -1;
  }
}
.logo-text__image {
  min-width: 101px;
  min-height: 18px;
  align-items: center;
  vertical-align: middle;
  padding-top: 5px;
}

