}

.main-nav--closed .main-nav__toggle {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: absolute;
  border: none;
  margin: 0;
  padding: 0;
  background: url(../img/logo-closed.svg) no-repeat;
  height: 20px;
  width: 25px;
  margin-left: auto;
  cursor: pointer;
  top: 24px;
  right: 20px;
}
.main-nav--closed .main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav--closed .main-nav__toggle:active {
  opacity: 0.6;
}
.main-nav--closed .main-nav__toggle:focus {
  opacity: 0.6;
}
@media (min-width: 708px) {
  .main-nav--closed .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1220px) {
  .main-nav--closed .main-nav__toggle {
    display: none;
  }
}

.main-nav--opened .main-nav__toggle {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: absolute;
  border: none;
  margin: 0;
  padding: 0;
  background: url(../img/logo-opened.svg) no-repeat;
  height: 20px;
  width: 19px;
  margin-left: auto;
  cursor: pointer;
  top: 21px;
  right: 23px;
}
.main-nav--opened .main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav--opened .main-nav__toggle:focus {
  opacity: 0.6;
}
.main-nav--opened .main-nav__toggle:active {
  opacity: 0.6;
}
@media (min-width: 708px) {
  .main-nav--opened .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1220px) {
  .main-nav--opened .main-nav__toggle {
    display: none;
  }
}

.main-nav--nojs .site-list {
  display: block;
  position: static;
  min-height: 0;
}

.main-nav--nojs .site-list__item {
  display: block;
}

.main-nav__wrapper {
  position: absolute;
  top: 65px;
  left: 0;
  width: 100%;
  background-color: #FFFFFF;
  z-index: 2;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;
  background-color: #FFFFFF;
}

.main-nav--opened .main-nav__wrapper {
  display: block;
}

.main-nav--closed .main-nav__wrapper {
  display: none;
}

.main-header__logo-img {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

@media (min-width: 708px) {
  .main-nav__wrapper {
    display: block;
    position: relative;
    top: 0;
    min-width: 440px;
    height: 52px;
  }

  .main-nav--closed .main-nav__wrapper {
    display: block;
  }

  .main-nav__list {
    display: flex;
    justify-content: space-between;
    min-width: 440px;
  }
}
@media (min-width: 1220px) {
  .main-nav__wrapper {
    display: block;
    max-width: 478px;
    position: relative;
    top: 0;
  }

  .main-nav--closed .main-nav__wrapper {
